.header{
    margin: 5px 15px 0;
    grid-area: header;
    background: linear-gradient(45deg, #767fa7, #c9eff6);
    padding: 5px;
    border-radius: 5px;
}

.header .logo img{
    max-width: 50px;
    float: left;
    margin: 5px 30px;
}

.loginBlock{
    margin-right: 30px;
    float: right;
    margin-top: 20px;
}

.loginBlock a{
    text-decoration: none;
    padding: 50px 0;
    color: #545466;
    font-size: 18px;
}

.loginBlock a:hover{
    color: #6c32f9;
}

.loginBlock .login {
    display: inline-block;
}

.loginBlock .login button{
    width: 80px;
    padding: 4px 7px;
    margin-left: 15px;
    background-color: #7d7ed7;
    border: none;
    color: white;
    text-decoration: none;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
}

.loginBlock .login button:hover{
    background-color: #6f51b2;
    color: white;
}

.loginBlock .loginBtn{
    width: 80px;
    padding: 4px 7px;
    margin-left: 15px;
    background-color: #4CAF50;
    border: none;
    color: white;
    text-decoration: none;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
}

.loginBlock .loginBtn:hover{
    background-color: #3ddf44;
    color: white;
}

