* {
    box-sizing: border-box;
}

.app-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
            "header header header"
            "sitebar content content";
    grid-template-columns: 2fr 10fr;
    grid-row-gap: 5px;
    grid-column-gap: 5px;
}

.app-wrapper-content {
    grid-area: content;
    margin: 0 5% 0 4%;
}

