.login {
    text-align: center;
    margin: 50px auto;
    color: #37374b;
}

.loginForm {
    margin: 0 auto;
    width: 35%;
    box-shadow: 0 2px 5px 5px rgba(75, 74, 96, 0.25);
    padding: 30px 10px;
}


.loginForm input{
    box-sizing: border-box;
    border: 1px solid #b2b2b2;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: #37374b;
    padding: 5px;
    margin: 10px 0;
    width: 70%;
}

.loginForm input:focus{
    box-shadow: 0 0 3px 2px rgba(102, 196, 235, 0.7);
    border: 1px solid rgba(25, 88, 175, 0.5);
}


.loginForm input[type="checkbox"] {
    width: 25px;
    font-size: 14px;
    margin: 20px 0;
}

.loginForm button{
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 5px 25px;
    text-decoration: none;
    margin: 0 5px;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
}

.loginForm button:hover{
    background-color: #3ddf44;
    color: white;
}

.loginForm button:active{
    background-color: #4CAF50;
}



