.paginator {
    margin: 30px 0 20px 13px;
    max-width: 78%;
}

@media (max-width: 1200px) {
    .paginator {
        max-width: 100%;
        margin: 30px 0 5px 5px;
    }
}

.paginator .pageNumber {
    display: inline-block;
    line-height: 25px;
    margin-right: 5px;
    font-size: 18px;
    cursor: pointer;
}

.paginator .pageNumber:last-child{
    margin-right: 0;
}

.paginator .pageNumber:hover {
    display: inline-block;
    line-height: 25px;
    margin-right: 5px;
    font-size: 18px;
    cursor: pointer;
}

.paginator .selectedPage {
    font-weight: bold;
    color: #ff5d28;
}

.paginator button {
    color: white;
    background-color: #7d7ed7;
    border: none;
    font-size: 16px;
    padding: 4px 15px;
    margin: 0 10px;
    cursor: pointer;
    vertical-align: top;
    transition: 0.25s;
    font-weight: 700;
}

.paginator button:hover{
    background-color: #6f51b2;
}

.paginator .begEnd {
    width: 70px;
    background-color: #7d7ed7;
    color: white;
    border: none;
    font-size: 14px;
    line-height: 1.3em;
    padding: 4px 15px;
    margin: 0 5px;
    cursor: pointer;
    vertical-align: top;
    transition: 0.25s;
    font-weight: 400;
}



