.navbar{
    grid-area: sitebar;
    background:  #767fa7;
    padding: 20px;
    min-height: 100vh;
    border-radius: 7px;
    margin: 0 15px 20px;
}

.menu {
    /*position: fixed;*/
    /*top: 105px;*/
    width: 9%;
}

.item {
    min-height: 35px;
    margin-bottom: 10px;
    padding: 5px;
    width: 140px;
}


.item:last-child{
    margin-top: 30px;
}

.item a{
    text-decoration: none;
    color: #fff;
    padding: 5px;
    text-transform: uppercase;
    font-family: Roboto, sans-serif;
    letter-spacing: 0.1em;
    transition: 0.3s;
}

.item a:hover{
    color: #ff8234;
    font-size: 17px;
}

.item a.active{
    color: #ff8234;
    font-weight: 700;
}

