.usersPage {
    margin: 20px 4% 20px 2%;
}

.userItem{
    margin: 25px 0;
}

.userItem .user{
    text-align: center;
    margin-right: 20px;
    float: left;
}

.userItem .user .ava img{
    margin-top: 15px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
}

.userItem .user button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 5px 10px;
    text-decoration: none;
    cursor: pointer;
    font-weight: 700;
    transition: 0.25s;
    margin-top: 10px;
    width: 80px;
}

.userItem .user button:hover{
    background-color: #3ddf44;
    color: white;
}

.userItem .user button:disabled{
    background-color: #545466;
    color: white;
}

.userItem .userInfo{
    margin-top: 20px;
    display: inline-block;
    padding: 10px;
    background-color: #e2e3ff;
    width: 65%;
    border-radius: 10px;
    border: 1px solid #dbdbdb;
}

.userInfo .name{
     float: left;
}

.userInfo .fullName{
    font-weight: 700;
}

.userInfo .status{
    color: #545466;
    margin-top: 40px;
}

.userInfo .location{
    text-align: right;
    float: right;
 }

.location .city{
    margin-top: 7px;
    margin-bottom: 30px;
}



